<template>
    <el-dialog title='新建' v-model="dialogForm" class="updatePwd">

        <el-form :model="form">
            <div class="radio-list">
                <el-form-item label="" :label-width="formLabelWidth">
                    <el-radio class="radio"  v-model="form.type" label="1">消息</el-radio>
                    <el-radio class="radio"  v-model="form.type" label="2">公告</el-radio>
                </el-form-item>
            </div>
            <el-form-item label="标题:" :label-width="formLabelWidth">
                <el-input v-model.trim="form.title" auto-complete="off" placeholder="必填"></el-input>
            </el-form-item>
            <el-form-item label="内容:" :label-width="formLabelWidth">
                <el-input v-model.trim="form.content" auto-complete="off" placeholder="必填"></el-input>
            </el-form-item>
            <el-form-item label="链接:" :label-width="formLabelWidth">
                <el-input v-model.trim="form.url" auto-complete="off"  placeholder="必填"></el-input>
            </el-form-item>
            <el-form-item label="发布时间" :label-width="formLabelWidth">
                <el-date-picker
                        v-model="form.push_time"
                        type="datetime"
                        placeholder="选择发布时间">
                </el-date-picker>
                <p>不选发布时间，立即发布。选择发布时间，所选发布时间发布</p>
            </el-form-item>
            <!--<el-form-item label="有效天数:" :label-width="formLabelWidth">-->
                <!--<el-input v-model.trim="form.expire_days" auto-complete="off" ></el-input>-->
            <!--</el-form-item>-->
            <div class="radio-list">
                <el-form-item label="平台:" :label-width="formLabelWidth">
                    <el-radio class="radio" v-model="form.sys" label="1">全部</el-radio>
                    <el-radio class="radio" v-model="form.sys" label="2">安卓</el-radio>
                    <el-radio class="radio" v-model="form.sys" label="3">IOS</el-radio>
                </el-form-item>
            </div>


           <!-- <el-form-item label="指定版本:" :label-width="formLabelWidth">
                <el-input v-model="form.app_versions" auto-complete="off"></el-input>
                <p>多个版本请用英文“,”号隔开, 不指定则推送所有版本，版本模式为：20302</p>
            </el-form-item>-->
          <!--  <div class="radio-list">
                <el-form-item label="徒弟情况:" :label-width="formLabelWidth">
                    <el-radio class="radio" v-model="form.pupil_type" label="1">全部</el-radio>
                    <el-radio class="radio" v-model="form.pupil_type" label="2">有徒弟</el-radio>
                    <el-radio class="radio" v-model="form.pupil_type" label="3">无徒弟</el-radio>
                </el-form-item>
            </div>
            <div class="radio-list">
                <el-form-item label="新老用户:" :label-width="formLabelWidth">
                    <el-radio class="radio" v-model="form.new_member_type" label="1">全部</el-radio>
                    <el-radio class="radio" v-model="form.new_member_type" label="2">新用户</el-radio>
                    <el-radio class="radio" v-model="form.new_member_type" label="2">老徒弟</el-radio>
                </el-form-item>
            </div>-->
            <el-form-item label="指定用户:" :label-width="formLabelWidth" v-show="isShow">
                <el-input v-model="form.member_id" auto-complete="off"></el-input>
                <p>多个用户请用英文“,”号隔开, 不指定则推送到所有用户</p>
            </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button @click="dialogForm = false">取 消</el-button>
            <el-button type="primary" @click="submit()">提交</el-button>
        </div>
    </el-dialog>
</template>

<style scope="" lang="less" type="text/scss">
    .updatePwd{
        .el-dialog__header{
            padding:30px;
            .el-dialog__headerbtn{
                width:50px;
                height:50px;
                margin:-30px -30px 0 0;
            }
        }
        .el-dialog__body{
            padding:30px 50px 0 0;
        }
    }
</style>

<script>
    import QuRequest from '../../core/QuRequest'

    const quRequest = new QuRequest()

    export default {
        data() {
            return {
                radio:1,
                dialogForm: false,
                formLabelWidth: '200px',
                radioShow:false,
                disabled:true,
                isShow:true,
                form:{
                    type:'1',
                    content: '',
                    title: '',
                    url: '',
                    push_time: '',
                    sys: '1',
                    pupil_type: '1',
                    new_member_type: '1',
                    member_id: '',
                }
            }
        },
        methods: {
            showDialog(type){
                this.type = type;
                this.dialogForm = true;
                this.form.title = '';
                this.form.content = '';
                this.form.url = '';
                this.form.push_time = '';
                this.form.type = '1';
                this.form.member_id ='';
            },
            submit(){
                let that = this;
                quRequest.send(quRequest.Apis.qutuiba.message.save,this.form
                ).then(res => {
                    this.$message.success("提交成功！");
                }).catch(err => {
                    this.$message.error(`获取内容列表数据失败：${ err.message || '未知错误' }`);
                }).fin(() => {
                    this.$emit('close',that.type);
                    this.hideDialog();
                }).done();

            },
            hideDialog(){
                this.dialogForm = false;
            }
        }
    }
</script>


